import React from "react";
import "./VideoStory.sass"

interface IProps
{
  videoSrc: string;
  text: any;
  header: string;
  footer?: string;
  headerRight?: any
}

export class VideoStory  extends React.Component<IProps, any>
{
  render() 
  {
    return (
      <div className="video-story-container story-1">
        
        <div className="video-story-text">
          <div className="video-story-header">
            <div>
              <h3>{this.props.header}</h3>
            </div>
            <div>
              {
                this.props.headerRight != null ? this.props.headerRight : ""
              }
            </div>
          </div>
          <span>
            <div className="video">
              <iframe src={this.props.videoSrc} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            </div>
            <div>{this.props.text}</div>
          </span>
          <span>
            {
              this.props.footer != null ? this.props.footer : ""
            }
          </span>
        </div>
      </div>
    )
  }
}