import React from 'react';
import { ICustomerStory, IGatsbyLocation } from '../../interfaces';
import { graphql } from 'gatsby';
import { CustomerStory } from '../../components/CustomerStory/CustomerStory';
import { ContentLayoutWithSidebar } from '../../components/Layout/ContentLayout';
import { SideBar } from '../../components/SideBar/SideBar';
import { SideBarHelp } from '../../components/SideBar/SideBarHelp';
import { CategoryBar } from '../../components/CategoryBar/CategoryBar';
import { IHeaderProps } from '../../components/Layout/Header';
import { VideoStory } from '../../components/CustomerStory/VideoStory';
import { FaArrowRight } from 'react-icons/fa';
import { Link } from '../../components/Misc';

class CustomerStoriesPage extends React.Component<{stories: ICustomerStory[], location: IGatsbyLocation}>
{
  render()
  {
    let header: IHeaderProps = {
      title: "Kundenmeinungen",
      url: "kundenmeinungen",
      description: "Was unsere Kunden über uns sagen",
      type: "website"
    };
    let stories = this.props.stories
      .sort((a,b) => a.fields.story.company > b.fields.story.company ? 1 : -1)
      .sort((a,b) => a.fields.story.order > b.fields.story.order ? 1 : -1)
      
    return (
      <ContentLayoutWithSidebar categoryBar={<CategoryBar title="404 Not Found"></CategoryBar>} header={header} sidebar={
        <SideBar isFixed={true}>
          <SideBarHelp></SideBarHelp>
        </SideBar>
      }>
        <div>
          <div>
            <div>
              <h1>Fallbeispiele</h1>
            </div>
            <VideoStory 
              header="Potential-Analyse" 
              text={(
                <>
                  <p>"Kunden, die kauften, kauften auch..." - diese Datenanalyse kennen wir alle. Im hier vorgestellten Fall standen wir vor einer ähnlichen Aufgabe: Unser Kunde vertreibt eigene und zugekaufte Produkte an lokale Ladengeschäfte (Händler).
                  Um Potentiale für den die Händler zu ermitteln, haben wir eine Analyse entwickelt, die einen Shop mit einer definierbaren Menge anderer Shops vergleicht und Produkte ermittelt, die der aktuelle Shop weniger verkauft als die Vergleichsgruppe - oder vielleicht überhaupt nicht im Sortiment hat. Auf diese Weise können dem Händler mögliche Potentiale aufgedeckt werden.
                  </p>
                  <p>
                    Im Video gehen wir auf die Details ein. Oben rechts gelangen Sie direkt zum (anonymisierten) Ergebnis.
                  </p>
                </>
              )}
              videoSrc="https://www.youtube.com/embed/2hbFLDLBb5E"
              headerRight={(
                <Link isOutbound={true} to="https://app.powerbi.com/view?r=eyJrIjoiOGFkYzFkMjYtMmFkNC00MWUzLWE5YmItZTZlMjM5NDcxYTNmIiwidCI6ImFmZDlkMmFjLTEyYTktNDhjYy05MDA4LWEzMDQ2Y2FlNWM4NyIsImMiOjh9" 
                  ><FaArrowRight style={{fontSize: ".7em", marginRight: "5px"}}>
                  </FaArrowRight>
                  Live-Demo
                </Link>
              )}
              >
            </VideoStory>
          </div>
          <div>
            <div>
              <h1>Was unsere Kunden über uns sagen</h1>
            </div>
            {
              stories.map((s,i) => (
                <div key={i} className={(i%2 == 0 ? "story-1" : "story-2")}>
                  <CustomerStory story={s} location={this.props.location}></CustomerStory>
                </div>
              ))
            }
          </div>
        </div>
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({data, pageContext, location}) => {
  return (
    <CustomerStoriesPage stories={data.stories.edges.map(e => e.node)} location={location}></CustomerStoriesPage>
  );
};
export default Page;

export const pageQuery = graphql`
  {
    ...customerStories
  }
`;
